export default {
	data() {
		return {
			
		};
	},
	props: {
		type: {
			type: String,
			default: 'seller'
		},
		userData: {
			type: Object,
			default: ()=>({})
		}
	},
	methods: {
		settingHandle(){
			this.$emit('on-click-setting')
		},
		getLevel(num){
			if(num <= 2000){
				return "大众猎手"
			}else if(5000 >= num >2000 ){
				return "铜牌猎手"
			}else if(10000 >= num > 5000){
				return "银牌猎手"
			}else if(num > 10000){
				return "金牌猎手"
			}
		}
	}
}
